.auth-container {
  min-height: 100vh;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.auth-container::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("https://images.unsplash.com/photo-1454165804606-c3d57bc86b40?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -1;
  filter: brightness(0.4);
}

.auth-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0;
  max-width: 1200px;
  width: 100%;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.auth-notes {
  padding: 3rem;
  background: #00b894;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-notes h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  font-weight: 700;
}

.auth-notes h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.auth-notes p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.auth-features {
  margin-top: 2rem;
}

.auth-features h4 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.auth-features ul {
  list-style: none;
  padding: 0;
}

.auth-features li {
  margin-bottom: 0.8rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.auth-features li::before {
  content: "âœ“";
  color: #00b894;
  background: white;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
}

.auth {
  padding: 3rem;
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.auth-header {
  color: #2d3436;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
}

.auth form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.auth__button {
  background: #00b894 !important;
  border-color: #00b894 !important;
  color: white !important;
  padding: 1rem !important;
  border-radius: 8px !important;
  font-size: 1.1rem !important;
  font-weight: 600 !important;
  transition: all 0.3s ease !important;
  margin-top: 6.5rem !important;
  margin-right: 3rem !important;
}

.auth__button:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.3) !important;
}

.switch__button {
  background: transparent !important;
  border: 1px solid #00b894 !important;
  color: #2d3436 !important;
  padding: 1rem !important;
  border-radius: 8px !important;
  margin-top: 1rem !important;
  font-size: 1rem !important;
  transition: all 0.3s ease !important;
  margin-right: 2.8rem !important;
}

.switch__button:hover {
  background: #00b894 !important;
  color: white !important;
}

.password-input-container {
  position: relative;
  width: 100%;
}

.password-toggle-auth {
  position: absolute;
  right: 1rem;
  top: 65%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  color: #2d3436;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.password-toggle-auth:hover {
  color: #00b894;
}

.password-toggle-auth svg {
  width: 1.5rem;
  height: 1.5rem;
}

/* Form Input Styles */
.auth input {
  width: 100%;
  padding: 1rem;
  border: 1px solid #dfe6e9;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.auth input:focus {
  border-color: #00b894;
  box-shadow: 0 0 0 2px rgba(0, 184, 148, 0.2);
  outline: none;
}

/* Image Upload Styles */
.image-upload {
  margin: 1rem 0;
  padding: 1rem;
  border: 2px dashed #dfe6e9;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.image-upload:hover {
  border-color: #00b894;
  background: rgba(0, 184, 148, 0.05);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .auth-content {
    grid-template-columns: 1fr;
    max-width: 600px;
  }

  .auth-notes {
    padding: 2rem;
  }

  .auth {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
  .auth-container {
    padding: 1rem;
  }

  .auth-notes h2 {
    font-size: 2rem;
  }

  .auth-notes h3 {
    font-size: 1.3rem;
  }

  .auth-header {
    font-size: 1.8rem;
  }
}

.forgot-password-link {
  margin-top: 0.5rem;
  text-align: right;
}

.forgot-password-link a {
  color: #00b894;
  text-decoration: underline;
  font-size: 0.98rem;
  cursor: pointer;
  transition: color 0.2s;
}

.forgot-password-link a:hover {
  color: #098c6c;
}

/* Success message for reset/forgot password */
.auth .success-message {
  color: green;
  margin: 1rem 0;
  text-align: center;
  font-weight: 500;
}
