.form-control__image {
  padding: 0;
  margin: 1rem 0;
}

.image-upload.center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.image-upload__preview {
  width: 13rem;
  height: 13rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 50%;
  background: #f8fafc;
  border: 2px dashed #00b894;
  margin-bottom: 1rem;
  overflow: hidden;
  transition: all 0.3s ease;
}

.image-upload__preview:hover {
  border-color: #00b894;
  background: rgba(0, 184, 148, 0.05);
}

.image-upload__preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.form-control__button {
  margin-right: 5rem;
  background: transparent;
  border: 1px solid #00b894;
  color: #2d3436;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.form-control__button:hover {
  background: #00b894;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.3);
}
